.user-list{
  @media (max-width: @mobile-width-only){
    display: block;
  }
  @media (min-width: @tablet-width){
  display:flex;
  align-items: center;
  order:1;
  height: 89px;
  }
  @media (min-width: @desktop-width){
    order:3;
  }
}
.user-list__item{
  @media (min-width: @tablet-width){
    height: 100%;

  }
}

.user-list__icon{
  opacity: 0.2;
  @media (max-width: @mobile-width-only){
    padding:0 25px 0 0;
  }
  box-sizing: content-box;
  padding: 0 30px;
  z-index: 1;
}
.user-list__search{
  @media (min-width: @tablet-width){
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
}

}
.user-list__container{
  @media (min-width: @tablet-width){
  display:none;
  }
}
.user-list__icon:hover{
  color: @downy;
  opacity: 1;
}
