.main-nav{
  @media (max-width: @mobile-width-only) {
    position: relative;
    height: fit-content;
    background-color: @white;
  }
  height: 100%;
  @media (min-width: @tablet-width) {
    height: 89px;
    max-height: 134px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }
  @media (min-width: @desktop-width) {
    position: relative;
  }
}
.main-nav__link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: @black;
  text-decoration: none;
  display:flex;
  align-items: center;
}
.main-nav__link:hover .user-list__icon{
  color: @downy;
  opacity: 1;
}
.main-nav__link:focus .user-list__icon{
  color: @downy;
  opacity: 0.3;
}


.main-nav__toggle {
  position: absolute;
  top: 32px;
  right: 0;
  padding:0;
  z-index: 2;
  display: block;
  width: 20px;
  height: 14px;
  background-color: @white;
  cursor: pointer;
  border: none;
  @media (min-width: @tablet-width){
    display: none;
  }
 }



.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background-color: @black;
  box-shadow: 0 6px 0 0 @black, 0 12px 0 0 @black;
  @media (min-width: @tablet-width){
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  position: absolute;
  top: 0;
  right: 14px;
  width: 21px;
  height: 20px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 37px;
    width: 27px;
    height: 2px;
    background-color: @black;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: fade(@black, 30%);
  }
  @media (min-width: @tablet-width){
    display: none;
  }

}
@media (max-width: @mobile-width-only) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .main-nav--closed .user-list__item {
    display: none;
  }
}
.main-nav__logo{
  display:none;
  @media (min-width: @desktop-width){
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.main-nav__menu-img{
  @media (max-width: @mobile-width-only) {
    align-self: center;
  }
}
.main-nav__list{
  max-width: 50%;
  .no-markers-list;
  margin:0;
  display:none;
}

.main-nav__note{
  display:none;
  color: @tundora;
  font-size: 15px;
  line-height: 24px;
  @media (min-width: @tablet-width){
    display:block;
    order:3;
    margin-top: 20px;
  }
  @media (min-width: @desktop-width){
    order:5;
  }
}
.main-nav__item{
  @media (max-width: @mobile-width-only){
    display: flex;
    flex-wrap: wrap;
    width:100vw;
    margin-left:-30px;
    padding: 0 30px;
    justify-content: flex-start;
    height: 79px;
    background-color: @white;
    border-top: 1px solid rgba(0,0,0,0.1);
  }
  display:flex;
}


